import "./styles.css";
import React, { useState } from "react";

import TodoList from "./TodoList";

function App() {
  const [todoList, setTodoList] = useState([]);
  const [toAddTodo, setToAddTodo] = useState("");
  const [countUnCompleted, setCountUnCompleted] = useState(0);

  const addTodo = (toAddTodo) => {
    if (toAddTodo == null || toAddTodo.trim() === "") return;
    let tempList = [...todoList];
    tempList = [
      ...tempList,
      { id: todoList.length + 1, task: toAddTodo, complete: false }
    ];
    setTodoList(tempList);
  };

  const handleToggle = (id) => {
    let mapped = todoList.map((task) => {
      return task.id === Number(id)
        ? { ...task, complete: !task.complete }
        : { ...task };
    });
    setTodoList(mapped);
    doFiltered(mapped);
  };

  const doFiltered = (mapped) => {
    let filtered = mapped.filter((task) => {
      return task.complete === false;
    });
    console.log(filtered);
    setCountUnCompleted(filtered.length);
  };

  const handleChange = (event) => {
    setToAddTodo(event.currentTarget.value);
  };

  const handleAdd = (event) => {
    event.preventDefault();
    addTodo(toAddTodo);
    setToAddTodo("");
    if (toAddTodo == null || toAddTodo.trim() === "") return;
    setCountUnCompleted(countUnCompleted + 1);
  };

  const handleReset = () => {
    setTodoList([]);
    setCountUnCompleted(0);
  };

  return (
    <div className="App">
      <h1>TODO List (963702 saisuda.b)</h1>
      <form onSubmit={handleAdd}>
        <input
          value={toAddTodo}
          type="text"
          onChange={handleChange}
          placeholder="please add task"
        />
        <button>Add</button>
      </form>
      {countUnCompleted} remaining out of {todoList.length} tasks
      <br />
      <br />
      <TodoList todoList={todoList} handleToggle={handleToggle} />
      <br />
      <br />
      <button onClick={() => handleReset()}>Reset</button>
    </div>
  );
}

export default App;
