import React from "react";

const Todo = ({ todo, handleToggle }) => {
  const handleClick = (event) => {
    event.preventDefault();
    handleToggle(event.currentTarget.id);
  };

  return (
    <div>
      <li
        id={todo.id}
        key={todo.id + todo.task}
        name="todo"
        onClick={handleClick}
        className={todo.complete ? "strike todo" : "todo"}
      >
        {" "}
        {todo.task}
      </li>
    </div>
  );
};

export default Todo;
